@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400&display=swap");

.blog-cover {
  width: 85%;
  margin-top: 50px;
  height: 400px;
  z-index: 99;
  background-position: top;
  background-size: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.blog-read-content {
  width: 85%;
  padding-top: 40px;
  background-color: #f5f3f3;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 30px;
}
.blog-read-content > H1 {
  font-size: 40px;
  font-weight: bold;
  font-family: "Inter", sans-serif;
}
.blog-page-auther {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.auther-name {
  background-color: #f95d5d;
  padding: 5px 30px;
  border-radius: 15px;
  color: white;
  font-weight: bold;
  margin-right: 20px;
}
.blog-page-auther > h3 {
  font-size: 16px;
  font-weight: bold;
  color: #6c6c6c;
  font-family: "Inter", sans-serif;
}
.blog-content {
  width: 95%;
}
.blog-content > p {
  margin: 10px 0px;
}
