@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400&display=swap");
.home-page-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.navbar {
  height: 60px;
  width: 50px;
  width: 100%;
  background-color: #6c6c6c;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.nav-item-wrapper {
  width: 85%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.nav-logo-holder img {
  width: 50px;
}
.nav-login-signup {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.nav-login-signup > p {
  margin-right: 10px;
  color: white;
  font-weight: bold;
}
.home-hero-banner {
  cursor: pointer;
  margin-top: 50px;
  width: 100%;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-card-wrapper {
  width: 85%;
  min-height: 150px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  background-color: #f5f3f3;
  filter: drop-shadow(5px 5px 3px rgba(0, 0, 0, 0.25));
  transition: ease-in-out all 0.5s;
}
.hero-card-wrapper:hover {
  filter: drop-shadow(10px 10px 3px rgba(0, 0, 0, 0.25));
  transition: ease-in-out all 0.5s;
}
.hero-img {
  background-image: url("https://cdn.pixabay.com/photo/2020/11/05/19/29/woman-5716038_1280.jpg");
  /* width: 40%;
  height: 100%; */
  background-position: center;
  background-size: cover;
  height: 250px;
  width: 400px;
}
.hero-banner-contents {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 250px;
  margin-left: 20px;
}
.latest-tag {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 30px;
  background-color: #f95d5d;
  color: white;
  border-radius: 30px;
  font-weight: bold;
}
.hero-heading {
  margin-top: 5px;
  font-family: "Inter", sans-serif;
}
.author {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.profile-picture {
  width: 50px;
  height: 50px;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  border: 3px solid #f95d5d;
}
.profile-name-date {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}
.profile-name-date > h3 {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #6c6c6c;
}
.blog-content {
  margin-top: 10px;
  width: 90%;
}
.section-heading {
  width: 100%;
  margin: 40px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-heading > h2 {
  width: 85%;
  font-family: "Inter", sans-serif;
}
.blog-body-wrapper {
  width: 85%;
}
.body-grid {
  display: grid;
  width: 85%;
  column-gap: 10px;
  row-gap: 30px;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto;
}
.Link {
  text-decoration: none;
  margin-right: 10px;
  color: white;
  font-weight: bold;
  transition: ease-in-out all 0.5s;
}
.Link:hover {
  color: #f95d5d;
  transition: ease-in-out all 0.5s;
}
/* ------------ Card Design ---------------- */
.grid-card {
  cursor: pointer;
  height: 300px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #f5f3f3;
  filter: drop-shadow(5px 5px 3px rgba(0, 0, 0, 0.25));
  transition: ease-in-out all 0.5s;
}
.grid-card:hover {
  filter: drop-shadow(10px 10px 3px rgba(0, 0, 0, 0.25));
  transition: ease-in-out all 0.5s;
}
.grid-item-img {
  width: 100%;
  height: 200px;
  background-position: center;
  background-size: cover;
}
.card-title {
  width: 80%;
  margin-left: 15px;
  margin-top: 10px;
  font-size: 13px;
  font-family: "Inter", sans-serif;
}
.grid-card > .author {
  margin-left: 10px;
}
.cardLink {
  text-decoration: none;
  color: #6c6c6c;
}
/* -------------- Footer -------------- */
.footer {
  margin-top: 20px;
  height: 50px;
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f5f3f3;
  background-color: #6c6c6c;
}

/*----------------Profile-----------------*/

.wrapper {
  position: relative;
  top: 10px;
  width: 650px;
  display: flex;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
}

.wrapper .left {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  background: #6c6c6c;
  padding: 30px 25px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;

  text-align: center;
  color: #fff;
}

.wrapper .left img {
  border-radius: 5px;
  margin-bottom: 10px;
}

.wrapper .left h4 {
  margin-bottom: 10px;
}

.wrapper .left p {
  font-size: 12px;
}

.wrapper .right {
  width: 65%;
  background: #fff;
  padding: 30px 25px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.wrapper .right .info,
.wrapper .right .projects {
  margin-bottom: 15px;
}

.wrapper .right .info h3,
.wrapper .right .projects h3 {
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 1px solid #e0e0e0;
  color: #353c4e;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.wrapper .right .info_data,
.wrapper .right .projects_data {
  display: flex;
  justify-content: space-between;
}

.wrapper .right .info_data .data,
.wrapper .right .projects_data .data {
  width: 45%;
}

.wrapper .right .info_data .data h4,
.wrapper .right .projects_data .data h4 {
  color: #353c4e;
  margin-bottom: 5px;
}

.wrapper .right .info_data .data p,
.wrapper .right .projects_data .data p {
  font-size: 13px;
  margin-bottom: 10px;
  color: #919aa3;
}

.wrapper .social_media ul {
  display: flex;
}

.wrapper .social_media ul li {
  width: 45px;
  height: 45px;
  background: #6c6c6c;
  margin-right: 10px;
  border-radius: 5px;
  text-align: center;
  line-height: 45px;
}

.wrapper .social_media ul li a {
  color: #fff;
  display: block;
  font-size: 18px;
}

.rection-heading {
  width: 100%;
  margin: 40px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.rection-heading > h2 {
  padding-top: 10px;
  width: 85%;
  font-family: "Inter", sans-serif;
}
