@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400&display=swap");

.write-blog-body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.blog-body-wrapper {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.blog-header {
  position: absolute;
  background-color: #6c6c6c;
  height: 300px;
  width: 100%;
}
.blog-contents {
  margin-top: 50px;
  background-color: #f5f3f3;
  width: 85%;
  padding: 10px;
  height: 1030px;
  z-index: 99;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.blog-contents > h2 {
  margin-top: 20px;
  font-family: "Inter", sans-serif;
  font-size: 30px;
  font-weight: bold;
  color: #6c6c6c;
}
.divider {
  margin-top: 10px;
  border-bottom: 3px solid #6c6c6c;
  width: 200px;
}
.blog-input {
  margin-top: 20px;
  width: 80%;
  font-size: 20px;
  font-family: "Inter", sans-serif;
  border-radius: 5px;
  padding: 10px;
  border: 2px solid #6c6c6c;
  background-color: rgba(0, 0, 0, 0.25);
  color: white;
}
.blog-text {
  margin-top: 20px;
  width: 80%;
  font-size: 20px;
  font-family: "Inter", sans-serif;
  border-radius: 5px;
  padding: 10px;
  border: 2px solid #6c6c6c;
  background-color: rgba(0, 0, 0, 0.25);
  color: white;
  height: 700px;
}
.blog-sbt-btn {
  background-color: rgb(117, 248, 117);
  color: palevioletred;
  width: 80%;
  font-size: 20px;
  font-family: "Inter", sans-serif;
  font-weight: bold;
  text-align: center;
  margin-top: 15px;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid rgb(46, 245, 46);
  cursor: pointer;
  transition: ease-in-out all 0.5s;
}
.blog-sbt-btn:hover {
  border: 2px solid palevioletred;
  transition: ease-in-out all 0.5s;
}
